@charset "UTF-8";

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  line-height: 1;
  font-size: 0.32rem;
  font-weight: 400;
  -webkit-tap-highlight-color: transparent;
}

html,
#app {
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: #fff;
  font-family: 'Avenir', 'PingFangSC-Regular', Helvetica, Arial, sans-serif;
  color: #333;
  font-size: 0.32rem;
  position: relative;
  -webkit-overflow-scrolling: touch;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: none;
}

img {
  border: 0;
}

ol,
ul,
li {
  list-style: none;
}

i {
  font-style: normal;
}

h1,
h2,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-size: 0.32rem;
}

a {
  text-decoration: none;
  color: #333;
  cursor: pointer;
}

label {
  cursor: pointer;
}

button,
input[type="button"] {
  border: none;
  background: #fff;
}

button:hover {
  cursor: pointer;
  text-align: center;
}

input[type="button"]:hover {
  cursor: pointer;
}

input {
  border: none;
}

input:disabled {
  background-color: #fff;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  text-align: center;
}

th,
td {
  font-weight: normal;
  font-size: 0.32rem;
}

.clear:before,
.clear:after {
  content: '';
  display: block;
  clear: both;
  height: 0;
}

.tabs-bg-active,
.bi-content .bi-bt-item.bi-bt-item-active:after {
  background: linear-gradient(to right, #43D7F6, #2650B1);
}

/* 头部 */

.bi-container {
  max-width: 10rem;
  margin: 0 auto;
  background-color: #061027;
  padding-top: 1.48rem;
}

.bi-container .bi-tabs {
  height: 1.08rem;
  width: 100%;
  max-width: 10rem;
  overflow-x: scroll;
  overflow-y: hidden;
  border-bottom: 1px solid #1F2E4F;
  position: fixed;
  left: 50%;
  top: 0;
  z-index: 9999;
  background-color: #0C1735;
  transform: translateX(-50%);
}

.bi-container .bt-inner {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-flex;
  white-space: nowrap;
  padding: 0 0.4rem;
}

.bi-container .bt-item {
  color: rgba(255, 255, 255, 0.5);
  height: 1.066667rem;
  line-height: 1.066667rem;
  margin-left: 0.4rem;
  font-size: 0.373333rem;
}

.bi-container .bt-item:first-child {
  margin-left: 0;
}

.bi-container .bt-item.bt-item-active {
  color: #409EEA;
}

/* 内容 */

.bi-content {
  background-color: #0B1134;
}

.bi-content .bi-type {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to right, #152847, #0B1134);
  padding: 0.4rem 0;
  font-size: 0.373333rem;
  margin-bottom: 0.4rem;
}

.bi-content .bi-type-li {
  flex: 1;
  color: rgba(255, 255, 255, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.bi-content .bi-type-li-icon {
  display: block;
  width: 0.933333rem;
  margin-bottom: 0.266667rem;
}

.bi-content .bi-bli-title {
  display: flex;
  align-items: center;
  padding: 0.266667rem 0.4rem;
  background-color: #192744;
  border-top-left-radius: 0.133333rem;
  border-top-right-radius: 0.133333rem;
  border-top: 1px solid #283D5E;
  overflow: hidden;
  position: sticky;
  top: 1.026667rem;
  z-index: 999;
}

.bi-content .bi-bli-icon {
  display: block;
  width: 0.533333rem;
}

.bi-content .bi-bli-text {
  color: #DAE1E9;
  padding-left: 0.133333rem;
  font-size: 0.373333rem;
}

.bi-content .bi-bl-desc {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.373333rem;
  height: 1.066667rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.4rem;
}

.bi-content .bi-bl-full {
  width: 0.666667rem;
  height: 0.666667rem;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100%;
  position: relative;
  top: 0.066667rem;
}

.bi-content .bi-bl-tabs {
  height: 1.12rem;
  width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
  position: relative;
  background-color: rgba(0, 0, 0, 0.1);
}

.bi-content .bi-bt-inner {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-flex;
  white-space: nowrap;
  padding: 0 0.4rem;
}

.bi-content .bi-bt-item {
  color: rgba(255, 255, 255, 0.5);
  height: 1.12rem;
  line-height: 1.12rem;
  margin-left: 0.666667rem;
  font-size: 0.4rem;
}

.bi-content .bi-bt-item:first-child {
  margin-left: 0;
}

.bi-content .bi-bt-item.bi-bt-item-active {
  color: #409EEA;
  position: relative;
}

.bi-content .bi-bt-item.bi-bt-item-active:after {
  content: '';
  display: block;
  width: 110%;
  height: 0.066667rem;
  position: absolute;
  left: -5%;
  bottom: 0;
}

.bi-content .bi-charts-inner {
  background-color: #0C1238;
  overflow: hidden;
}

.bi-content .bi-chart {
  margin-bottom: 0.4rem;
}

/* 易湃 */

.bi-bli-flat {
  padding: 0.4rem 0.4rem 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.bi-bli-flat-wrap {
  flex: 0 0 48%;
  margin-bottom: 0.4rem;
  background-color: #0F2141;
  border-radius: 0.106667rem;
  padding: 0.266667rem 0.133333rem 0.2rem;
}

.bi-bli-flat-title {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.346667rem;
  padding-left: 0.266667rem;
}

.bi-bli-flat-num {
  text-align: center;
  color: #fff;
  font-size: 0.48rem;
  padding: 0.4rem 0;
}

.bi-bli-flat-data {
  display: flex;
  justify-content: space-between;
}

.bi-bli-flat-dl,
.bi-bli-flat-dr {
  flex: 1;
  display: flex;
  align-items: center;
  font-size: 0.266667rem;
  color: rgba(255, 255, 255, 0.5);
}

.bi-bli-flat-dr {
  justify-content: flex-end;
}

.bi-bli-flat-icon {
  width: 0.346667rem;
  height: 0.346667rem;
  margin-right: 0.066667rem;
}

.bi-bli-flat-icon.bi-bli-flat-down {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100%;
}

.bi-bli-flat-icon.bi-bli-flat-up {
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100%;
}

/* 详情 */

.detail-container {
  height: 100%;
  background-color: #0D1835;
  display: flex;
  flex-direction: column;
  position: relative;
}

.detail-header {
  height: 1.2rem;
  padding: 0 0.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.detail-header-title {
  flex: 1;
  color: #fff;
  font-size: 0.373333rem;
}

.detail-header-group {
  width: 2.666667rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.detail-header-icon-list {
  width: 0.666667rem;
  height: 0.666667rem;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100%;
}

.detail-header-icon-close {
  width: 0.933333rem;
  height: 0.933333rem;
  background: url(../img/bi-icon-close.24cb88ce.png) no-repeat center center;
  background-size: 100%;
}

.detail-gc-content {
  flex: 1;
}

/* 列表 */

.detail-list {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.detail-list-outer {
  width: 75%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: #0C1A38;
  border-left: 1px solid #56B9FF;
  overflow-x: hidden;
  overflow-y: scroll;
}

.detail-list-table {
  width: 100%;
  color: #fff;
}

.detail-list-table tbody tr:nth-child(odd) {
  background-color: #1C255C;
}

.detail-list-table th {
  font-size: 0.346667rem;
}

.detail-list-table th,
.detail-list-table td {
  padding: 0.266667rem 0;
}

